@use '../../index.scss' as *;

main.layoutmain {
    @include boxshadow($accent-color-darker);
    @include background_noise($accent-color-lightest);
    margin: 1rem;
    color: $accent-color-darkest;

    section {
        margin: 0.5rem 2rem 2rem 2rem;
    }
}

nav.layoutnav {
    @include boxshadow($accent-color-darker);
    @include background_noise($accent-color);
    margin: 2rem 1rem;

    ul {
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 0;
        list-style: none;
        min-height: 3rem;
    }

    li {
        height: 3rem;
        width: 100%;
    }
    li.logo {
        z-index: 1;
        flex: 1;
        margin: 0 0 2em 0;
    }
    li.navlink {
        position: relative;
        flex: 1;
    }

    li a {
        position: relative;
        display: inline-block;
        text-align: center;
        cursor: pointer;
        text-decoration: none;
        color: $accent-color-darkest;
        font-size: 1.3rem;
        height: 100%;
        width: 100%;
        vertical-align: middle;
        line-height: 3rem;
    }

    li.logo a {
        white-space: nowrap;
        font-size: 5.2rem;
        margin-top: -0.5rem;
    }
    
    li.current a {
        color: $accent-color-lightest;
        background: $accent-color-darker;
        text-decoration: underline;
    }

    @media (hover: hover) {
        li:not(.current).navlink:hover a {
            color: $accent-color-darkest;
        }
        li:not(.current).navlink:hover a::after {
            content: '';
            position: absolute;
            width: calc(100% - 0.5rem);
            height: calc(100% - 0.5rem);
            top: 0.25rem;
            right: 0.25rem;
            bottom: 0.25rem;
            left: 0.25rem;
            background-color: $accent-color-darker;
            opacity: 0.2;
            border-radius: 0.2rem;
        }
        li.navlink:active {
            background: $accent-color-darkest;
        }
        li:not(.current).navlink:active a {
            color: $accent-color-lightest;
        }
    }

    @media only screen and (min-width: 768px) {
        ul {
            flex-direction: row;
        }

        li.logo {
            margin: 0;
        }

        li.logo a {
            width: min-content;
            margin-left: 0.5rem;
            padding: 0 1rem;
        }

        li.navlink {
            flex: 0 1 140px;
        }

        li:not(.current).navlink:not(:last-child):after {
            content: '';
            opacity: 0.2;
            position: absolute;
            height: 60%;
            border-left: 2px solid $accent-color-darker;
            width: 1px;
            top: 20%;
            right: -2px;
        }
    }
}
